import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { routing } from './pages.routing';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { LayoutModule } from '../shared/layout.module';
import { SharedModule } from '../shared/shared.module';
import {WppagesComponent}  from '../pages/wppages/wppages.component';
import { PostEditComponent } from './wppages/post-edit/post-edit.component';
import { PostListComponent } from './wppages/post-list/post-list.component';
import { UserListComponent } from './wppages/user-list/user-list.component';
import { PostNewComponent } from './wppages/post-new/post-new.component';

import { Http } from '@angular/http';
import { HttpClientModule, HttpClient } from '@angular/common/http';

/* components */
import { PagesComponent } from './pages.component';
import { LoginComponent } from './login/login.component';
import {AuthenticationComponent } from './authentication/authentication.component'

 import {
  WpApiModule,
  WpApiLoader,
  WpApiStaticLoader
} from 'wp-api-angular';


  export function WpApiLoaderFactory(http: Http) {

  return new WpApiStaticLoader(http, 'http://10.0.2.2/Wp-Apitest/wp-json/wp/v2/', '');
}


@NgModule({
    imports: [
        CommonModule,
        LayoutModule,
        SharedModule,
        routing,
        FormsModule,
        ReactiveFormsModule,

       // WppagesModule,
         WpApiModule.forRoot({
          provide: WpApiLoader,
          useFactory: (WpApiLoaderFactory),
          deps: [Http]
        })

    ],
    declarations: [
        PagesComponent,
        LoginComponent,
        AuthenticationComponent,
        WppagesComponent,
        PostEditComponent,
        PostListComponent,
        UserListComponent,
        PostNewComponent,


    ]
})
export class PagesModule { }



